<template>
  <div :class="`item-container ${subItem ? 'subitem' : ''}`" v-if="item">
    <div class="item" :id="`item-${item.timestamp}`">
      <div class="drag-area">
        <svg
          width="9"
          height="12"
          viewBox="0 0 9 12"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M1.5 10.5H7.5M1.5 1.5H7.5H1.5ZM1.5 6H7.5H1.5Z"
            stroke="#383C4C"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </div>
      <div class="delete-item-btn" @click="deleteItem(index)">
        <svg
          width="12"
          height="16"
          viewBox="0 0 12 16"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M3.33301 3.99998V2.66665C3.33301 2.31302 3.47348 1.97389 3.72353 1.72384C3.97358 1.47379 4.31272 1.33331 4.66634 1.33331H7.33301C7.68663 1.33331 8.02577 1.47379 8.27582 1.72384C8.52587 1.97389 8.66634 2.31302 8.66634 2.66665V3.99998M10.6663 3.99998V13.3333C10.6663 13.6869 10.5259 14.0261 10.2758 14.2761C10.0258 14.5262 9.68663 14.6666 9.33301 14.6666H2.66634C2.31272 14.6666 1.97358 14.5262 1.72353 14.2761C1.47348 14.0261 1.33301 13.6869 1.33301 13.3333V3.99998H10.6663Z"
            stroke="#9598A7"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </div>
      <div class="arrows">
        <div class="arrow-up" v-if="index > 0" @click="moveUp(index)"></div>
        <div
          class="arrow-down"
          v-if="index < totalLength - 1"
          @click="moveDown(index)"
        >
          
        </div>
      </div>
      <div class="item-inputs">
        <!-- TITLE -->
        <div
          :class="`form-floating mb-3 title ${
            localTitle && localTitle.length >= 1 ? '' : 'empty'
          }`"
          @click.stop="titleFocus = !titleFocus"
        >
          <div class="relative" v-on-clickaway="clickOutside">
            <input
              type="text"
              v-model="item.title"
              class="title-input-item"
              @keydown.esc="titleFocus = false"
            />
            <label
              for="floatingInput"
              class="title-label-item"
              v-show="!titleFocus"
            >
              Nzov poloky
            </label>
            <!-- <div class="title-results">
              <div
                v-for="item in titleOptions[0].items"
                :key="item.id"
                @click="selectItemLocal(item)"
              >
                {{ item.title }}
              </div>
            </div> -->

            <div
              class="results-absolute"
              v-show="titleFocus && filteredOptions.length > 0"
            >
              <div class="results-group-title">DATABASE OF ITEMS</div>
              <div
                v-for="item in filteredOptions"
                :key="item.id"
                class="flex items-center justify-between results-group-item"
                @click="selectItemLocal(item)"
              >
                <div class="item-info">
                  <div class="item-title">{{ item.title }}</div>
                  <div class="item-description" v-html="item.richText"></div>
                </div>
                <div class="flex items-center justify-center item-price">
                  <span v-if="!item.total.from">
                    {{ item.total }} <span>{{ currencySymbol }}</span>
                  </span>
                  <span v-else-if="item.total.from && item.total.to">
                    {{ item.total.from }}{{ currencySymbol }} -
                    {{ item.total.to }}{{ currencySymbol }}
                  </span>
                </div>
              </div>
              <div class="results-group-title">HISTORY</div>
              <div
                v-for="item in recentItems"
                :key="'recent-' + item.id"
                class="flex items-center justify-between results-group-item"
                @click="selectItemLocal(item)"
              >
                <div class="item-info">
                  <div class="item-title">{{ item.title }}</div>
                  <div class="item-description" v-html="item.richText"></div>
                </div>
                <div class="flex items-center justify-center item-price">
                  <span v-if="!item.total.from">
                    {{ item.total }} <span>{{ currencySymbol }}</span>
                  </span>
                  <span v-else-if="item.total.from && item.total.to">
                    {{ item.total.from }}{{ currencySymbol }} -
                    {{ item.total.to }}{{ currencySymbol }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- TYPE SELECT -->
        <div class="form-floating type-item">
          <Multiselect
            v-model="item.type"
            :options="item.types"
            placeholder=""
            label="label"
            value="value"
            track-by="label"
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            deselectGroupLabel=""
            selectGroupLabel=""
            :allowEmpty="false"
          >
          </Multiselect>
          <label for="floatingInput" style="top: -13px"> Cena </label>
        </div>

        <!-- PRICE FROM -->
        <div class="input-number-container" v-if="itemTypeValue == 2">
          <div
            :class="`form-floating mb-3 input-after price currency-${currency}`"
            :data-currency="currencySymbol"
          >
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.priceFrom"
            />
            <label for="floatingInput">cena od</label>
          </div>
          <div class="plus-minus">
            <div
              class="plus"
              @click="item.priceFrom = parseFloat(item.priceFrom) + 1"
            >
              +
            </div>
            <div class="minus" @click="item.priceFrom -= 1">-</div>
          </div>
        </div>

        <!-- PRICE TO -->
        <div class="input-number-container" v-if="itemTypeValue == 2">
          <div
            :class="`form-floating mb-3 input-after price currency-${currency}`"
            :data-currency="currencySymbol"
          >
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.priceTo"
            />
            <label for="floatingInput">cena do</label>
          </div>
          <div class="plus-minus">
            <div
              class="plus"
              @click="item.priceTo = parseFloat(item.priceTo) + 1"
            >
              +
            </div>
            <div class="minus" @click="item.priceTo -= 1">-</div>
          </div>
        </div>

        <!-- HOURS FROM -->
        <div
          class="input-number-container"
          v-if="
            itemTypeValue != 5 &&
            itemTypeValue != 1 &&
            itemTypeValue != 2 &&
            itemTypeValue != 4
          "
        >
          <div class="form-floating mb-3 input-after hours">
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.hoursFrom"
            />
            <label for="floatingInput">poet od</label>
          </div>
          <div class="plus-minus">
            <div
              class="plus"
              @click="item.hoursFrom = parseFloat(item.hoursFrom) + 1"
            >
              +
            </div>
            <div class="minus" @click="item.hoursFrom -= 1">-</div>
          </div>
        </div>

        <!-- HOURS TO -->
        <div
          class="input-number-container"
          v-if="
            itemTypeValue != 5 &&
            itemTypeValue != 1 &&
            itemTypeValue != 2 &&
            itemTypeValue != 4
          "
        >
          <div class="form-floating mb-3 input-after hours">
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.hoursTo"
            />
            <label for="floatingInput">poet do</label>
          </div>
          <div class="plus-minus">
            <div
              class="plus"
              @click="item.hoursTo = parseFloat(item.hoursTo) + 1"
            >
              +
            </div>
            <div class="minus" @click="item.hoursTo -= 1">-</div>
          </div>
        </div>

        <!-- HOURS -->
        <div
          class="input-number-container"
          v-if="
            itemTypeValue != 5 &&
            itemTypeValue != 2 &&
            itemTypeValue != 3 &&
            itemTypeValue != 4
          "
        >
          <div class="form-floating mb-3 input-after hours">
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.hours"
            />
            <label for="floatingInput">poet</label>
          </div>
          <div class="plus-minus">
            <div class="plus" @click="item.hours = parseFloat(item.hours) + 1">
              +
            </div>
            <div class="minus" @click="item.hours -= 1">-</div>
          </div>
        </div>

        <!-- RATE EURO/HOUR -->
        <div
          class="input-number-container"
          v-if="itemTypeValue != 5 && itemTypeValue != 2 && itemTypeValue != 4"
        >
          <div
            :class="`form-floating mb-3 input-after rate currency-${currency}`"
            :data-rate="`${currencySymbol}/hod`"
          >
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.rate"
            />
            <label for="floatingInput">sadzba</label>
          </div>
          <div class="plus-minus">
            <div class="plus" @click="item.rate = parseFloat(item.rate) + 1">
              +
            </div>
            <div class="minus" @click="item.rate -= 1">-</div>
          </div>
        </div>

        <!-- UNIT -->

        <div v-if="itemTypeValue === 4" class="form-floating unit-select">
          <Multiselect
            v-model="item.unit"
            :options="unitTypes"
            label="label"
            value="value"
            track-by="label"
            placeholder=""
            selectLabel=""
            selectedLabel=""
            deselectLabel=""
            deselectGroupLabel=""
            selectGroupLabel=""
            :allowEmpty="false"
            :show-labels="false"
            :show-no-results="false"
            :show-no-options="false"
            @search-change="unitChange"
            :preserveSearch="true"
            @close="closeUnitDD"
          >
            <span slot="noResult">
              Oops! No elements found. Consider changing the search query.
            </span>
          </Multiselect>
          <label for="floatingInput" style="top: -13px"> Jednotka </label>
        </div>

        <!-- COUNT -->
        <div class="input-number-container" v-if="itemTypeValue === 4">
          <div
            class="form-floating mb-3 input-after pocet"
            :data-unit="this.item.unit ? this.item.unit.label : ''"
          >
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.count"
            />
            <label for="floatingInput">Pocet</label>
          </div>
          <div class="plus-minus">
            <div class="plus" @click="item.count = parseFloat(item.count) + 1">
              +
            </div>
            <div class="minus" @click="item.count -= 1">-</div>
          </div>
        </div>

        <!-- VALUE -->
        <div class="input-number-container" v-if="itemTypeValue === 4">
          <div
            :class="`form-floating mb-3 input-after rate rate-unit currency-${currency}`"
            :data-rate-unit="`${
              this.item.unit && item.unit.label
                ? `${currencySymbol}/${this.item.unit.label}`
                : ''
            }`"
          >
            <input
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
              v-model="item.value"
            />
            <label for="floatingInput">sadzba</label>
          </div>
          <div class="plus-minus">
            <div class="plus" @click="item.value = parseFloat(item.value) + 1">
              +
            </div>
            <div class="minus" @click="item.value -= 1">-</div>
          </div>
        </div>
      </div>

      <VueEditor
        :editorToolbar="customToolbar"
        v-model="item.richText"
        placeholder="Popis poloky"
      />

      <div class="buttons-cont">
        <div>
          <div class="d-flex justify-content-between bottom-buttons">
            <div
              class="d-flex justify-content-between inner"
              v-if="!item.subItems || item.subItems.length <= 0"
            >
              <div
                class="add-subitem-btn white-btn"
                @click="addSubItem(index)"
                v-if="!subItem"
              >
                + Prida podpoloku
              </div>

              <div
                class="add-images-btn transparent-btn"
                @click="showFileUpload = !showFileUpload"
              >
                <span style="margin-right: 3px"
                  ><svg
                    width="12"
                    height="10"
                    viewBox="0 0 12 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fill-rule="evenodd"
                      clip-rule="evenodd"
                      d="M1.79999 0.100006C1.42869 0.100006 1.0726 0.247506 0.810044 0.510057C0.547493 0.772607 0.399994 1.1287 0.399994 1.50001V8.50001C0.399994 8.87131 0.547493 9.2274 0.810044 9.48995C1.0726 9.75251 1.42869 9.90001 1.79999 9.90001H10.2C10.5713 9.90001 10.9274 9.75251 11.1899 9.48995C11.4525 9.2274 11.6 8.87131 11.6 8.50001V1.50001C11.6 1.1287 11.4525 0.772607 11.1899 0.510057C10.9274 0.247506 10.5713 0.100006 10.2 0.100006H1.79999ZM10.2 8.50001H1.79999L4.59999 2.90001L6.69999 7.10001L8.09999 4.30001L10.2 8.50001Z"
                      fill="#383C4C"
                    />
                  </svg>
                </span>
                <span v-if="!showFileUpload"> Prida obrzky k poloke </span>
                <span v-else
                  >Odstrni obrzky ({{ imagesUploadedLength }}/10)</span
                ><img
                  :src="i"
                  class="i-icon"
                  data-bs-toggle="tooltip"
                  style="margin-left: 5px"
                  data-bs-placement="top"
                  title="tooltip on top"
                />
              </div>
            </div>
          </div>
        </div>
        <div>
          <div class="total-item">
            Cena: <b>{{ totalText }}</b>
          </div>

          <div class="do-not-count-in-total-toggle root-toggle-count-total">
            <Toggle
              title="Nezapota do hlavnej ceny"
              :value="item.doNotCountInTotal"
              :update="(value) => (item.doNotCountInTotal = value)"
              :stacked="true"
            />
            <img
              :src="i"
              class="i-icon"
              data-bs-toggle="tooltip"
              data-bs-placement="top"
              title="tooltip on top"
            />
          </div>
        </div>
      </div>
      <div v-if="!subItem" class="subitems">
        <Draggable v-model="item.subItems" :handle="'.drag-area'">
          <transition-group>
            <Item
              v-for="(subItem, index2) in item.subItems"
              :key="`sub-${index}-${index2}`"
              :subItem="true"
              :deleteItem="(index) => deleteItem(index, item.subItems)"
              :index="index2"
              :moveUp="(index) => moveUp(index, item.subItems)"
              :moveDown="(index) => moveDown(index, item.subItems)"
              :totalLength="item.subItems.length"
              :currency="currency"
              :parentIndex="index"
              :currencySymbol="currencySymbol"
              :titleOptions="titleOptions"
              :setTitleOptions="setTitleOptions"
              :historyOfItems="historyOfItems"
              :selectItem="selectItem"
              :remoteItem="subItem"
            >
            </Item>
          </transition-group>
        </Draggable>
      </div>

      <div
        class="d-flex justify-content-between bottom-buttons bottom"
        v-if="item.subItems && item.subItems.length > 0"
      >
        <div class="d-flex justify-content-between inner">
          <div
            class="add-subitem-btn white-btn"
            @click="addSubItem(index)"
            v-if="!subItem"
          >
            + Prida podpoloku
          </div>

          <div
            class="add-images-btn transparent-btn"
            @click="showFileUpload = !showFileUpload"
          >
            <span style="margin-right: 3px"
              ><svg
                width="12"
                height="10"
                viewBox="0 0 12 10"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M1.79999 0.100006C1.42869 0.100006 1.0726 0.247506 0.810044 0.510057C0.547493 0.772607 0.399994 1.1287 0.399994 1.50001V8.50001C0.399994 8.87131 0.547493 9.2274 0.810044 9.48995C1.0726 9.75251 1.42869 9.90001 1.79999 9.90001H10.2C10.5713 9.90001 10.9274 9.75251 11.1899 9.48995C11.4525 9.2274 11.6 8.87131 11.6 8.50001V1.50001C11.6 1.1287 11.4525 0.772607 11.1899 0.510057C10.9274 0.247506 10.5713 0.100006 10.2 0.100006H1.79999ZM10.2 8.50001H1.79999L4.59999 2.90001L6.69999 7.10001L8.09999 4.30001L10.2 8.50001Z"
                  fill="#383C4C"
                />
              </svg>
            </span>
            <span v-if="!showFileUpload"> Prida obrzky k poloke </span>
            <span v-else>Odstrni obrzky ({{ imagesUploadedLength }}/10)</span
            ><img
              :src="i"
              class="i-icon"
              data-bs-toggle="tooltip"
              style="margin-left: 5px"
              data-bs-placement="top"
              title="tooltip on top"
            />
          </div>
        </div>
      </div>

      <div
        class="upload-files-cloud"
        @click="triggerFileUpload"
        v-if="showFileUpload"
      >
        <svg
          width="29"
          height="27"
          viewBox="0 0 29 27"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
        >
          <rect width="29" height="27" fill="url(#pattern0)" />
          <defs>
            <pattern
              id="pattern0"
              patternContentUnits="objectBoundingBox"
              width="1"
              height="1"
            >
              <use
                xlink:href="#image0_1_1477"
                transform="translate(-0.767979 -0.401506) scale(0.00672384 0.0074954)"
              />
            </pattern>
            <image
              id="image0_1_1477"
              width="364"
              height="226"
              xlink:href="data:image/png;base64,"
            />
          </defs>
        </svg>
        <span class="text">Viac obrzkov u nahra neme</span>
      </div>
      <div :class="`file-outer ${subItem ? 'sub' : 'master'}`">
        <input
          type="file"
          name="files"
          :class="`files ${subItem ? 'sub' : 'master'}`"
        />
      </div>
    </div>
    <div class="item-bottom-container" v-if="!subItem">
      <div class="left">
        <div class="inner">
          <input
            class="internal-note"
            v-model="item.note"
            placeholder="Prida intern poznmku...."
          />

          <div class="discount-container">
            <div class="discount-button">
              <svg
                width="14"
                height="14"
                viewBox="0 0 14 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                style="margin-right: 5px"
              >
                <path
                  d="M3.66667 3.66667H3.67333H3.66667ZM3.66667 1H7C7.34133 1 7.68267 1.13 7.94267 1.39067L12.6093 6.05733C12.8593 6.30737 12.9997 6.64645 12.9997 7C12.9997 7.35355 12.8593 7.69263 12.6093 7.94267L7.94267 12.6093C7.69263 12.8593 7.35355 12.9997 7 12.9997C6.64645 12.9997 6.30737 12.8593 6.05733 12.6093L1.39067 7.94267C1.26667 7.81901 1.16833 7.67208 1.10128 7.51031C1.03424 7.34854 0.99982 7.17511 1 7V3.66667C1 2.95942 1.28095 2.28115 1.78105 1.78105C2.28115 1.28095 2.95942 1 3.66667 1V1Z"
                  stroke="#383C4C"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
              <span @click="item.hasDiscount = !item.hasDiscount">
                <span v-if="!item.hasDiscount">Prida zavu poloky</span>
                <span v-else>Vymaza zavu poloky</span>
              </span>
            </div>
            <div v-if="item.hasDiscount" class="discount-input-container">
              <!-- <input v-model="item.discount" /> -->
              <div class="form-floating mb-3 input-after percent">
                <input
                  type="text"
                  class="form-control"
                  id="floatingInput"
                  v-model="item.discount"
                  max="100"
                />
              </div>
              <div class="plus-minus">
                <div
                  class="plus"
                  @click="item.discount = parseFloat(item.discount) + 10"
                >
                  +
                </div>
                <div class="minus" @click="item.discount -= 10">-</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="inner">
          <div class="grand-total">
            Cena spolu:
            <b>
              <del v-if="this.item.hasDiscount" style="margin-right: 10px">
                {{ totalWithSubitems.from }} {{ currencySymbol }}
                <span v-if="totalWithSubitems.from !== totalWithSubitems.to">
                  - {{ totalWithSubitems.to }} {currencySymbol}
                </span>
              </del>

              <span v-if="this.item.hasDiscount">
                {{ totalWithSubitemsWithDiscount.from }} {{ currencySymbol }}
                <span
                  v-if="
                    totalWithSubitemsWithDiscount.from !==
                    totalWithSubitemsWithDiscount.to
                  "
                >
                  - {{ totalWithSubitemsWithDiscount.to }} {{ currencySymbol }}
                </span>
              </span>
              <span v-else>
                {{ totalWithSubitems.from }} {{ currencySymbol }}
                <span v-if="totalWithSubitems.from !== totalWithSubitems.to">
                  - {{ totalWithSubitems.to }} {{ currencySymbol }}
                </span>
              </span>
            </b>
          </div>
          <!-- <div class="grand-total">Cena spolu: <b>125  - 150 </b></div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Draggable from "vuedraggable";
import { VueEditor } from "vue2-editor";
import { directive as onClickaway } from "vue-clickaway";

import i from "@/assets/svg/i.svg";
import Toggle from "./Toggle.vue";
import { fileUploaderOptionsThumbnail } from "@/assets/constants.js";

export default {
  name: "Item",
  directives: {
    onClickaway: onClickaway,
  },
  data() {
    return {
      i,
      unitTypes: [
        {
          value: 1,
          label: "ks",
        },
        {
          value: 2,
          label: "MD",
        },
        {
          value: 3,
          label: "MH",
        },
        {
          value: 4,
          label: "Ns",
        },
      ],
      customToolbar: [
        ["bold", "italic", "underline", "link"],
        [{ list: "ordered" }, { list: "bullet" }],
      ],
      showFileUpload: false,
      imagesUploadedLength: 0,
      selectedQuoteId: 0,
      localTitle: "",
      justSelectedItem: false,
      selectedItemId: -1,
      item: null,
      startUp: true,
      titleFocus: false,
    };
  },
  components: {
    Draggable,
    Toggle,
    VueEditor,
  },
  props: {
    deleteItem: {
      required: true,
      type: Function,
    },
    index: {
      required: true,
      type: Number,
    },
    parentIndex: {
      required: false,
      type: Number,
    },
    subItem: {
      required: false,
      default: false,
      type: Boolean,
    },
    moveUp: {
      required: true,
      type: Function,
    },
    moveDown: {
      required: true,
      type: Function,
    },
    totalLength: {
      required: true,
      type: Number,
    },
    addSubItem: {
      required: false,
      type: Function,
    },
    currency: {
      required: false,
      type: String,
    },
    currencySymbol: {
      required: false,
      type: String,
    },
    titleOptions: {
      required: true,
      type: Array,
    },
    setTitleOptions: {
      required: true,
      type: Function,
    },
    selectItem: {
      required: true,
      type: Function,
    },
    remoteItem: {
      required: true,
      type: Object,
    },
    historyOfItems: {
      required: true,
      type: Array,
    },
  },
  watch: {
    "item.rate"() {
      this.item.rate = parseInt(this.item.rate);
    },
    "item.hoursFrom"() {
      this.item.hoursFrom = parseInt(this.item.hoursFrom);
    },
    "item.hoursTo"() {
      this.item.hoursTo = parseInt(this.item.hoursTo);
    },
    total() {
      this.item.total = this.total;
    },
    totalWithSubitems() {
      this.item.totalWithSubitems = this.totalWithSubitems;
    },
    totalWithSubitemsWithDiscount() {
      this.item.totalWithSubitemsWithDiscount =
        this.totalWithSubitemsWithDiscount;
    },
    "item.hasDiscount"() {
      this.item.discount = 0;
    },
    item: {
      deep: true,
      // immediate: true,
      handler() {
        console.log("_____________");
        console.log(
          "item changed!!",
          "title: ",
          this.item.title,
          "this.subitem: ",
          this.subItem,
          `${this.parentIndex}-${this.index}`,
          "selected item id is : ",
          this.selectedItemId,
          "startUp is: ",
          this.startUp
        );

        // console.log("selectedItemId is: ", this.selectedItemId);

        if (this.startUp && !this.justSelectedItem) {
          this.selectedItemId = -1;
          this.startUp = false;
          return;
        } else if (this.startUp && this.justSelectedItem) {
          this.justSelectedItem = false;
          setTimeout(() => (this.selectedItemId = this.item.id), 400);
          return;
        }

        console.log("went past startup check");

        if (this.item.id === -1 || this.selectedItemId >= 0) {
          console.log("save new itemm, index: ", this.index);
          console.log("parentindex", this.parentIndex);
          // save new item
          const newId = !this.subItem
            ? this.setTitleOptions(
                {
                  ...this.item,
                  id: null,
                },
                this.index
              )
            : this.setTitleOptions(
                null,
                this.parentIndex,
                {
                  ...this.item,
                  id: null,
                },
                this.index
              );
          if (this.item.id !== -1) this.selectedItemId = -1;
          console.log("setting this.item.id = newId");
          this.item.id = newId;
          // give the new id to selectedQuoteId
          // this.selectedItemId = newId;
        } else {
          //else update new item
          console.log("updateItem, index:", this.index);
          if (!this.subItem) this.setTitleOptions(this.item, this.index);
          else
            this.setTitleOptions(null, this.parentIndex, this.item, this.index);
        }
        console.log("__________________");
      },
    },
    remoteItem: {
      deep: true,
      handler() {
        console.log(
          "remote item changed",
          this.remoteItem,
          this.subItem,
          `${this.parentIndex}-${this.index}`
        );

        this.selectedItemId = 0;
        this.startUp = true;
        this.item = JSON.parse(JSON.stringify(this.remoteItem));
        console.log("selectedItemId is: ", this.selectedItemId);
      },
    },
  },
  computed: {
    recentItems() {
      const recent = [];

      this.historyOfItems?.forEach((item) => {
        const found = this.titleOptions?.[0]?.items.find((itm) => {
          return itm.id === item;
        });

        if (found) recent.push(found);
      });

      return recent.slice(0, 3);
    },
    itemTypeValue() {
      if (!this.item) return null;
      return this.item.type?.value || this.item.type;
    },
    itemTotalFrom() {
      if (!this.item) return null;
      return this.item.hoursFrom * this.item.rate;
    },
    itemTotalTo() {
      if (!this.item) return null;
      return this.item.hoursTo * this.item.rate;
    },
    total() {
      if (!this.item) return null;
      const type = this.itemTypeValue;
      let total;

      // Price per hour
      if (type === 1) total = this.item.hours * this.item.rate;
      else if (type === 2)
        total = { from: this.item.priceFrom, to: this.item.priceTo };
      else if (type === 3)
        total = {
          from: (this.item.hoursFrom * this.item.rate).toFixed(2),
          to: (this.item.hoursTo * this.item.rate).toFixed(2),
        };
      else if (type === 4) total = this.item.count * this.item.value;
      else total = 0;

      return total;
    },
    totalText() {
      if (!this.item) return null;
      const total = this.total;

      if ((total.from && total.to) || total.from === 0 || total.to === 0)
        return `${total.from} ${this.currencySymbol} - ${total.to} ${this.currencySymbol}`;
      else return `${total} ${this.currencySymbol}`;
    },
    totalWithSubitems() {
      if (!this.item) return null;
      let sum;
      if (this.item.doNotCountInTotal) sum = { from: 0, to: 0 };
      else {
        sum = {
          from:
            this.item.total.from !== undefined && this.item.total.from !== null
              ? parseFloat(this.item.total.from)
              : parseFloat(this.item.total),
          to:
            this.item.total.to !== undefined && this.item.total.to !== null
              ? parseFloat(this.item.total.to)
              : parseFloat(this.item.total),
        };
      }

      this.item?.subItems?.forEach((subItem) => {
        if (!subItem.doNotCountInTotal) {
          sum.from +=
            subItem.total.from !== undefined && subItem.total.from !== null
              ? parseFloat(subItem.total.from)
              : parseFloat(subItem.total);

          sum.to +=
            subItem.total.to !== undefined && subItem.total.to !== null
              ? parseFloat(subItem.total.to)
              : parseFloat(subItem.total);
        }
      });

      return {
        from: parseFloat(sum.from.toFixed(2)),
        to: parseFloat(sum.to.toFixed(2)),
      };
    },
    totalWithSubitemsWithDiscount() {
      if (!this.item) return null;
      let sum = {
        from: this.totalWithSubitems.from,
        to: this.totalWithSubitems.to,
      };
      return {
        from: parseFloat(
          ((sum.from * (100 - this.item.discount)) / 100).toFixed(2)
        ),
        to: parseFloat(
          ((sum.to * (100 - this.item.discount)) / 100).toFixed(2)
        ),
      };
    },
    titleOptionsComputed() {
      if (!this.item) return null;
      return this.item.title?.length >= 1 ? this.titleOptions : [];
    },
    filteredOptions() {
      if (this.item.title.length <= 2) return [];

      return this.titleOptions?.[0]?.items
        ?.filter((item) => {
          return (
            item.title
              ?.toLowerCase()
              .includes(this.item.title?.toLowerCase()) ||
            item.richText
              ?.toLowerCase()
              .includes(this.item.title?.toLowerCase())
          );
        })
        .slice(0, 3);
    },
  },
  methods: {
    clickOutside() {
      console.log("click outside");
      this.titleFocus = false;
    },
    selectItemLocal(item) {
      console.log("select item:", item);
      console.log("this subitem", this.subItem);
      this.selectedItemId = item.id;
      this.justSelectedItem = true;
      // this.item.id = item.id
      // this.item.title = item.title;
      if (!this.subItem) this.selectItem(item, this.index);
      else {
        this.selectItem(item, this.parentIndex, this.index);
      }
    },
    closeTitleDropdown(val) {
      console.log("close dropdown val:", val);
      if (val?.name) val = val.name;

      // this.localTitle = val;
      this.item.title = this.localTitle || val;
    },
    closeUnitDD(val) {
      if (!val) return;
      if (typeof val === "object") return;

      setTimeout(() => {
        const unitObj = this.unitTypes.find((type) => type.label === val);

        let newLength;
        if (!unitObj) {
          const newUnit = {
            label: val,
            value: this.unitTypes.length,
          };

          newLength = this.unitTypes.push(newUnit);
        }

        this.item.unit = unitObj || this.unitTypes?.[newLength - 1];
      }, 80);
    },
    // addTag(one) {
    //   this.titleOptions?.[0]?.items.push({ name: one });
    // },
    // select(selectedOption) {
    //   this.justSelectedItem = true;

    //   setTimeout(() => {
    //     console.log(selectedOption);
    //     this.item.title = selectedOption.title;
    //     this.selectedQuoteId = 0;
    //     this.localTitle = this.item.title;
    //   }, 100);
    // },
    searchChange(searchQuery) {
      console.log("searchChange:", searchQuery);
      this.localTitle = searchQuery;

      // if (searchQuery?.length > 0) this.item.title = searchQuery;
      // if (typeof searchQuery === "object" && searchQuery)
      //   this.item.title = searchQuery;
    },
    unitChange(searchQuery) {
      if (typeof searchQuery === "object") return;
      if (searchQuery?.length > 0) this.item.unit = searchQuery;
    },
    enableTooltips() {
      var tooltipTriggerList = [].slice.call(
        document.querySelectorAll('[data-bs-toggle="tooltip"]')
      );
      tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new window.bootstrap.Tooltip(tooltipTriggerEl);
      });
    },
    enableFileUpload() {
      window
        .$(
          `#item-${this.item.timestamp} input.files.${
            this.subItem ? "sub" : "master"
          }`
        )
        .fileuploader({
          theme: "default",
          limit: 10,
          thumbnails: fileUploaderOptionsThumbnail,
          extensions: ["jpg", "jpeg", "png", "bmp", "gif", "svg"],
          // Callback fired on selecting and processing a file
          onSelect: () => {
            const input = document.querySelector(
              `#item-${this.item.timestamp} input.files.${
                this.subItem ? "sub" : "master"
              }`
            );

            this.item.images = input.files;
            console.log(this.item.images);
            this.imagesUploadedLength = input?.files?.length;
          },
          onRemove: () => {
            this.imagesUploadedLength -= 1;
          },
        });

      this.removeFileUploaderText();
      setTimeout(() => {
        this.removeFileUploaderText();
      }, 300);
    },
    removeFileUploaderText() {
      // remove text
      const fileUploader = document.querySelector(
        `#item-${this.item.timestamp} .fileuploader`
      );
      if (fileUploader) {
        // console.log(fileUploader.nextSibling);
        fileUploader?.nextSibling?.remove();
      }

      // const items = document.querySelectorAll(".item");
      // items?.forEach((item) => {
      //   item.outerHTML = item.outerHTML.replace(
      //     " innostudio.de  Fileuploader",
      //     ""
      //   );
      // });
    },
    triggerFileUpload() {
      const input = document.querySelector(
        `#item-${this.item.timestamp} .file-outer.${
          this.subItem ? "sub" : "master"
        } .fileuploader-input-caption`
      );
      console.log(input);
      input?.click();
    },
  },
  beforeMount() {
    console.log("beforeMount");
    this.item = JSON.parse(JSON.stringify(this.remoteItem));
  },
  mounted() {
    console.log("mounted!!!!!!!!");
    // this.item = this.remoteItem;
    this.enableTooltips();
    this.enableFileUpload();

    if (this.item.id !== -1) this.selectedItemId = this.item.id;
  },
};
</script>

