  }
}

.form-control:focus,
.form-select:focus {
  box-shadow: none;
  border-color: #eee;
}

.item-container {
  + .item-container {
    margin-top: 20px;
  }

  &.subitem {
    + .item-container.subitem {
      margin-top: 0px;
    }
  }
  .item-bottom-container {
    .form-control {
      &:focus {
        background: transparent;
        border: none;
      }
    }

    .discount-input-container {
      position: relative;

      .plus-minus {
        bottom: 0;
        background: transparent;
        right: -10px;
      }

      .input-after {
        &.percent {
          margin-bottom: 0 !important;
          border: none;

          &:after {
            content: "%";
            top: 0 !important;
            font-size: 13px;
            right: 20px;
          }

          input {
            border: none;
            outline: none !important;
            user-select: none;
            padding-top: 10px;
            width: 80px;
          }
        }
      }
    }
  }

  .plus-minus {
    position: absolute;
    top: 2px;
    bottom: 2px;
    right: 2px;
    width: 30px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    background: white;

    .plus,
    .minus {
      flex-grow: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      user-select: none;
    }
  }

  .input-after {
    &:after {
      content: "skata";
      position: absolute;
      color: $black;
      font-size: 10px;
      right: 0;
      top: 15px;
      bottom: 0;
      display: flex;
      z-index: 1;
      align-items: center;
    }

    input {
      user-select: none;
    }

    &.pocet {
      &:after {
        content: attr(data-unit);
      }
    }

    &.rate {
      &:after {
        content: attr(data-rate);
      }
    }

    &.rate-unit {
      &:after {
        content: attr(data-rate-unit) !important;
      }
    }

    &.hours {
      &:after {
        content: "hod";
      }
    }

    &.price {
      &:after {
